
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,font,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	font-weight: inherit;
	font-style: inherit;
	font-size: 100%;
	font-family: "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	max-width: 100vw;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

caption,th,td {
	text-align: left;
	font-weight: normal;
}

form legend {
	display: none;
}

blockquote:before,blockquote:after,q:before,q:after {
	content: "";
}

blockquote {
	font-size: 16px;
	border-radius: 10px;
	line-height: 2;
}

ol,ul {
	font-size: 16px;
	margin-left: 1em;
	line-height: 1.7;
}

hr {
	display: none;
	visibility: hidden;
}

:focus {
	outline: 0;
}

article {
	position: relative;
	top: 70px;
	bottom: 10px;
	max-width: 100vw;
	overflow-wrap: break-word;
	margin-left: 1em;
	margin-bottom: 5vh;
}

article h1,article h2,article h3,article h4,article h5,article h6 {
	color: #333;
	font-weight: bold;
	line-height: 1.25;
	margin-top: 1.3em;
}

article h2,article h3,article h4,article h5,article h6 {
	color: #d9aacd;
}

article h1 a,article h2 a,article h3 a,article h4 a,article h5 a,article h6 a {
	font-weight: inherit;
	color: #333;
}

article h1 a:hover,article h2 a:hover,article h3 a:hover,article h4 a:hover,article h5 a:hover,article h6 a:hover {
	color: #333;
}

article h1 {
	font-size: 36px;
	margin: 0 0 18px;
	border-bottom: 4px solid #eee;
}

article h2 {
	font-size: 28px;
	margin-bottom: 40px;
}

article h2 span{
	border-top: 4px solid #89c3eb;
	border-bottom: 4px solid #89c3eb;
}

article h2 span::before, article h2 span::after {
	content: "\2740";
}

article h3 {
	font-size: 20px;
	margin-bottom: 9px;
}

article h4 {
	font-size: 18px;
	margin-bottom: 3px;
}

article h5 {
	font-size: 18px;
	font-weight: normal;
	margin-bottom: 3px;
}

article .subheader {
	color: #777;
	font-weight: 300;
	margin-bottom: 24px;
}

article p {
	font-size: 16px;
	line-height: 1.7;
}

article p img {
	margin: 0;
}

article p.lead {
	font-size: 16px;
	font-size: 1.8rem;
	line-height: 1.5;
}

article em,article i {
	font-style: italic;
	line-height: inherit;
}

article strong,article b {
	font-weight: bold;
	line-height: inherit;
}

article small {
	font-size: 60%;
	line-height: inherit;
}

article h1 small,article h2 small,article h3 small,article h4 small,article h5 small {
	color: #777;
}

article hr {
	border: solid #ddd;
	border-width: 1px 0 0;
	clear: both;
	margin: 12px 0 18px;
	height: 0;
}

article abbr,article acronym {
	text-transform: uppercase;
	font-size: 90%;
	color: #222;
	border-bottom: 1px solid #ddd;
	cursor: help;
}

article abbr {
	text-transform: none;
}

article img {
	max-width: 100%;
}

article pre {
	margin: 10px 0;
	border: 1px solid #ddd;
	padding: 10px;
	background: #fafafa;
	color: #666;
	overflow: auto;
	border-radius: 5px;
}

article code {
	background: #fafafa;
	color: #666;
	font-family: inconsolata, monospace;
	border: 1px solid #ddd;
	border-radius: 3px;
	height: 4px;
	padding: 0;
}

article a code {
	color: #2083e0;
}

article a code:hover {
	color: #0060bb;
}

article pre code {
	border: 0;
	background: inherit;
	border-radius: 0;
	line-height: inherit;
	font-size: 14px;
}

article pre.prettyprint {
	border: 1px solid #ddd;
	padding: 10px;
}

article blockquote,article blockquote p {
	line-height: 1.25;
},article p.note {
	line-height: 20px;
	color: #4c4742;
}

article blockquote,article .note {
	margin: 0 0 18px;
	padding: 1px 20px;
	background: #fff7d6;
}

article blockquote li:before,article .note li:before {
	color: #e0bc6f;
}

article blockquote code,article .note code {
	background: #f5d899;
	border: none;
	color: inherit;
}

article blockquote a,article .note a {
	color: #0060bb;
}

article blockquote pre,article .note pre {
	background: #F5D899 !important;
	color: #48484C !important;
	border: none !important;
}

article p.note {
	padding: 15px 20px;
}

article table {
	width: 100%;
}

article table td {
	padding: 8px;
}

article table tr {
	background: #F4F4F7;
	border-bottom: 1px solid #eee;
}

article table tr:nth-of-type(odd) {
	background: #fafafa;
}

article dl dt {
	font-weight: bold;
}

article dl.tabbed {
	position: relative;
}

article dl.tabbed dt {
	float: left;
	margin: 0 5px 0 0;
	border: 1px solid #ddd;
	padding: 0 20px;
	line-height: 2;
	border-radius: 5px 5px 0 0;
}

article dl.tabbed dt a {
	display: block;
	height: 30px;
	color: #333;
	text-decoration: none;
}

article dl.tabbed dt.current {
	background: #f7f7f7;
}

article dl.tabbed dd {
	position: absolute;
	width: 100%;
	left: 0;
	top: 30px;
}

article dl.tabbed dd pre {
	margin-top: 0;
	border-top-left-radius: 0;
}

