.cell {
	position: relative;
	width: 20vw;
	height: 30vh;
	margin-left: 1em;
	margin-bottom: 5em;
	transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-webkit-transition: all 0.2s ease-in-out;
	text-decoration: none;
}

.cell:hover {
	transform-origin: center;
	transform: scale(1.05);
	-moz-transform: scale(1.05);
	-webkit-transform: scale(1.05);
	cursor: pointer;
}

.cell_container {
	display: inline-grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: autofit;
	position: relative;
	left: 25vw;
	top: 120px;
	width: 70vw;
}

.photo_container {
	position: fixed;
	bottom: 125px;
	left: 10px;
}

.photo {
	width: 15vw;
	height: 20vh;
	object-fit: cover;
	vertical-align: middle;
	border-radius: 10px;
	margin: 10px;
	box-shadow: 0 0 10px rgba(0,0,0,0.6);
	-moz-box-shadow: 0 0 10px rgba(0,0,0,0.6);
	-webkit-box-shadow: 0 0 10px rgba(0,0,0,0.6);
	
}

.photo_zoom_container {
	text-align: center;
	position: fixed;
	left: 0;
	top: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.8);
	backdrop-filter: blur(2px);
}

.photo_zoom {
	position: relative;
	top: 120px;
	width: 70vw;
	object-fit: scale-down;
	max-width: 70vw;
	max-height: 70vh;
}

.wrap {
	transition: all 0.2s ease-in-out;
}

.wrap:hover {
	cursor: zoom-in;
	transform: scale(1.05);
	-moz-transform: scale(1.05);
	-webkit-transform: scale(1.05);
}

.cell .img-container{
	height: 70%;
	overflow: hidden;
}

.cell img {
	object-fit: cover;
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
}

.cell .title {
	font-weight: bold;
	font-size: 150%;
}

.cell .content {
	color: #333;
}

.dark-content .cell .content {
