	background-color: rgba(255, 255, 255, 1);
	font-weight: bold;
	color: #333;
	text-decoration: none;
	border-radius: 20px;
	z-index: 1;
}

.original:hover {
	color: #333;
}

.photo_info {
	position: absolute;
	right: 20px;
	top: 150px;
	color: #eee;
	font-size: 28px;
	overflow-wrap: break-word;
	padding: 10px;
	max-width: 20vw;
}

.foot {
	position: absolute;
	bottom: 0;
}

.status {
	font-size: 70%;
	text-align: center;
	display: inline-block;
	border-radius: 13px;
	padding: 3px;
	margin: 5px;
	border: 1px solid;
	user-select: none;
}

.complete {
	color: #0de1bd;
	border-color: #0de1bd;
}

.progress {
	color: #b8a427;
	border-color: #b8a427;
}

.pend {
	color: #69675e;
	border-color: #69675e;
}

.stop {
	color: #f58c8c;
	border-color: #f58c8c;
}

.red-text {
	color: #f58c8c;
}

.green-text {
	color: #0de1bd;
}

.brown-text {
	color: #b8a427;
}

.fold-block {
	display: none;
}

.no-href-a {
	user-select: none;
	cursor: pointer;
	color: #2083e0;
}

.no-href-a:hover {
	color: #0060bb;
}

.border-text {
	text-stroke: 1px #333;
	-webkit-text-stroke: 0.2px #eee;
}

.menu-btn {
	display: none;
	height: 63px;
	width: 63px;
	justify-content: center;
	align-items: center;
}
.menu-btn span,
.menu-btn span:before,
.menu-btn span:after {
	content: '';
	display: block;
	height: 3px;
	width: 25px;
	border-radius: 3px;
	background-color: #eee;
	position: absolute;
}
.menu-btn span:before {
	bottom: 8px;
}
.menu-btn span:after {
	top: 8px;
}

.menu-bar {
	display: none;
	position: fixed;
	top: 63px;
	width: 100vw;
	height: auto;
}

.menu-item {
	color: #eee;
	padding-left: 30px;
	font-weight: bold;
	font-size: 20px;
	background-color: #172d57;
}

.menu-item-selected {
	background-color: #2b406b;
}

.menu-btn-selected {
	background-color: #172d57;
}

.moon {
	margin-left: -15px;
	margin-top: -25px;
	display: inline-block;
	height: 20px;
	width: 20px;
	border-radius: 50%;
	box-shadow: 5px 5px 0 0 #d0d5d2;
}

#lang-graph {
	position: fixed;
	top: 120px;
	left: 20px;
}

.arrow {
	height: 30px;
	width: 30px;
	border-radius: 30%;
	background-color: rgba(0, 0, 0, 0.2);
	transition: all 0.2s ease;
}

.arrow:hover {
	background-color: rgba(0, 0, 0, 0.6);
}

.repo-container {
	display: block;
	max-width: 100vw;
	overflow: hidden;
	margin-bottom: 50px;
}

.logo {
	object-fit: cover;
	max-width: 100%;
	max-height: 100%;
}

#head-logo {
	position: relative;
	top: 4px;
	left: 4px;
	height: 55px;
	width: 55px;
	display: inline-block;
	overflow: hidden;
}

.option {
	display: inline-block;
	vertical-align: middle;
	padding: 10px 10px 10px 0;
}

.option-container {
	cursor: pointer;
	user-select: none;
}

.option-container:hover:not(.option-selected) {
	background-color: rgba(209, 209, 209, 0.8);
}

.before-option {
	display: inline-block;
	vertical-align: middle;
	width: 10px;
	height: 10px;
	margin: auto 10px;
	background-size: cover;
	max-height: 100%;
}

.option-selected {
	background-color: #708090;
	color: #eee;
}

.dark-content .option-selected {
	background-color: #eee;
	color: #333;
}

.dark-content .option-container:hover:not(.option-selected) {
	background-color: #eee;
	color: #333;
}

#setting-bar {
	position: fixed;
	top: 64px;
	right: 0;
	height: calc(100% - 64px);
	width: 0;
	background-color: rgba(255, 255, 255, 1.0);
	transition: width 0.1s ease-in-out;
	border-radius: 15px 0 0 15px;
	overflow: auto;
	z-index: 1;
}

#setting-bar.slide-in {
	width: 20vw;
}

#setting-bar .thumb {
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
	width: 100%;
	height: 40%;
}

#setting-bar .before-option {
	height: 25px;
	width: 25px;
	margin: 0 20px;
}

#setting-bar .option {
	padding: 10px;
}

.dark-content #setting-bar {
	background-color: #708090;
}

#setting-bar h1 {
	font-size: 36px;
	font-weight: bold;
	padding: 10px;
}

.detail {
	padding: 10px;
	font-weight: bold;
	overflow: scroll;
	max-height: 20%;
	font-size: 150%;
	overflow-wrap: break-word;
}

.info {
	overflow-wrap: break-word;
	margin: 10px 20px;
}

.no-scroll-bar {
	overflow: scroll;
}

.no-scroll-bar::-webkit-scrollbar {
	display: none;
}

.action-btn {
	margin-left: 5px;
	margin-top: 5px;
	cursor: pointer;
}

#before-option-light {
	background-image: url('./assets/sun.svg');
}

#before-option-dark {
	background-image: url('./assets/moon.svg');
}

#before-option-auto {
	background-image: url('./assets/auto.svg');
}

#before-option-ja {
	background-image: url('./assets/ja.svg');
}

#before-option-en {
	background-image: url('./assets/en.svg');
}

#before-option-zh {
	background-image: url('./assets/zh.svg');
}

#title-right-setting {
	background-image: url('./assets/setting.svg');
}

#action-btn-close {
	content: url('./assets/close.svg');
}

@media only screen and (max-width: 425px) {
	.guide-container, .twitter-tl, .photo_container {
		display: none;
	}
	.menu-btn {
		display: inline-flex;
	}
	.cell_container {
		grid-template-columns: 1fr;
		left: 0;
		height: auto;
	}
	.cell {
		width: 85vw;
		margin-bottom: 3.5em;
	}
	#lang-graph {
		position: relative;
		width: 100vw;
		top: 120px;
		left: 0;
	}
	#langlist {
		position: relative;
		top: 100px;
	}
	#head-logo {
		display: none;
	}
	.wrapper .title-left:hover:not(.title-selected), .title-center:hover:not(.title-selected), .title-right:hover:not(.title-selected) {
		background-color: rgba(0, 0, 0, 0.0);
	}
	.wrapper .title-left:active:not(.title-selected), .title-center:active:not(.title-selected), .title-right:active:not(.title-selected) {
		background-color: rgba(0, 0, 0, 0.3);
	}
	.option-container:hover:not(.option-selected) {
		background-color: rgba(209, 209, 209, 0.0);
	}
	.option-container:active:not(.option-selected) {
		background-color: rgba(209, 209, 209, 0.8);
	}
	#setting-bar.slide-in {
		width: 100vw;
		border-radius: unset;
		background-color: rgba(255, 255, 255, 1.0);
	}
	.dark-content #setting-bar {
		background-color: #708090;
	}

}